// @file
// Style for menu items: positions, link styles.

// grid layout for menu items to allow grid layout of menu
// items. integrate with above if needed set desired/max rows and
// columns
$dcl-nav-menu-rows: 4;
$dcl-nav-menu-columns: 3;

.library-top-bar {
  a {
    color: $black;
    font-size: 110%;
  }
}

#dept-menu {
  padding: 5px 15px;
  li {
    border-bottom: 1px dotted $off-white;
  }
  a {
    color: $black;
  }
}

#dept-info {
  border-top: 3px solid $yellow;
  background-color: $off-white;
  h4 {
    margin-left: 2rem;
  }
  ul {
    list-style: none;
    li.address {
      margin: 1.5rem 0;
      p {
        margin: 0;
        padding: 0;
      }
    }
    li.social-links {
      margin: 1.5rem 0;
      a {
        text-indent: -9999px;
      }
      a[href^="https://www.facebook.com"]
        ::before {
        @extend %icon-facebook;

        width: 35px;
      }
      a::before {
        float: left;
        text-align: center;
        text-indent: 0;
        color: $gray;
        background: $yellow;
        font-size: 1.5rem;
      }
    }
  }
}

// Above here is old and can probably be removed
// Bootstrap DCL

// admin menus
#toolbar-administration {
  z-index: 10000;
}

.navbar-top-menu {
  border: none !important;
}

// the wrapper for the green bar in the banner
.primary-secondary-menus,
.region-mobile-menu {
  width: 100%;
  padding: 0 !important;
  background-color: $dart-green;
  a,
  span.dropdown-toggle {
    color: $white !important;
  }
  a:hover,
  a:focus,
  a:active {
    text-decoration: underline;
    background-color: transparent !important;
  }
  ul.contextual-links {
    a {
      color: $gray-dark !important;
    }
    a:hover,
    a:focus,
    a:active {
      text-decoration: underline;
      background-color: $teal !important;
    }
  }
}

.primary-secondary-menus .primary-secondary-menus-inner-wrapper {
  position: relative;
  width: 95%;
  max-width: 1500px;
  min-height: 85px;
  padding-top: 0;
  padding-bottom: 0;
}

#primary-secondary-menus-wrapper-mobile {
  overflow-y: scroll;
  background-color: $dart-green;
  -webkit-overflow-scrolling: touch;
  .mobile-branding {
    width: 90%;
    margin: 10px 0 0 10px;
  }
  ul.nav.menu {
    li {
      text-align: left !important;
    }
  }
  .region-secondary-menu ul li a {
    padding-left: 5px;
  }
  a {
    color: $white !important;
  }
  a:hover,
  a:active,
  a:focus {
    border-color: $white !important;
    background-color: transparent !important;
  }
}

.library-utility-menu {
  li {
    float: left;
    a {
      color: $black;
      font-size: 110%;
    }
  }
}

#primary-secondary-menus-wrapper {
  width: 100%;
  margin: 0;
}

// Make the bars for the toggle white
.navbar-toggle .icon-bar {
  background-color: $white;
}

// fix for the wrapped divs which have a display:block so that we can display navs in one line
.region-primary-menu,
.region-primary-menu nav,
.region-secondary-menu,
.region-secondary-menu nav {
  display: inline;
}

// make global nav menu display properly even when logged in
.region-primary-menu nav.contextual-region {
  position: static;
}

// primary-menu is the set of left hand links
// secondary menu is the right hand menu including the account dropdown

#block-mainnavigation-2 .nav,
#block-mainnavigation-3 .nav,
.region-primary-menu .nav {
  &.navbar-nav > li {
    a {
      text-transform: uppercase;
      color: $white;
      font-size: em($base-font-size);
    }
  }
}

#block-utilitymenu-3 {
  font-size: em($base-font-size);
}

.navbar-btn {
  margin-top: -16px;
}

.region-primary-menu {
  display: grid;
  grid-template-columns: fit-content(100%) 1fr fit-content(100%);
  align-content: center;
  width: 100%;
  margin-top: 7px;
  padding: 2px 0;
  column-gap: $base-font-size * 2;
  ul {
    margin-left: -5px !important;
  }
  &.dcl-scrolled {
    margin: -8px 0;
    padding: 0;
    -webkit-transition: all 1s ease;
    -moz-transition: all 1s ease;
    -o-transition: all 1s ease;
    transition: all 1s ease;
  }
}

.region-secondary-menu {
  float: right;
  font-size: em($base-font-size * 0.875);
  ul {
    margin-right: 5px !important;
  }
}

.region-primary-menu,
.region-secondary-menu {
  ul.nav.menu {
    li {
      text-align: center;
    }
  }
}

// make the underline a border and account for the additional height when the link is in hover
.region-primary-menu,
.region-secondary-menu,
#primary-secondary-menus-wrapper-mobile {
  ul {
    li {
      padding: 10px !important;
      a {
        padding: 0 !important;
      }
      a:hover,
      a:focus,
      a:active {
        margin-bottom: -1px !important;
        text-decoration: none;
        color: $spring-green;
        border-color: $spring-green;
      }
    }
  }
}

.region-secondary-menu li:last-of-type {
  padding-right: 12px !important;
}

#primary-secondary-menus-wrapper-mobile {
  a {
    display: inline !important;
  }
  #block-unifiedsearchform-2 {
    float: none !important;
  }
  #block-mainnavigation-2,
  #block-utilitymenu-3 {
    ul {
      margin-top: 0 !important;
    }
    ul.menu.dropdown-menu {
      margin-top: 0;
    }
  }
}

#primary-secondary-menus-wrapper-mobile {
  .region-mobile-menu {
    font-size: 1em;
    ul.nav.menu {
      margin: 0 !important;
      li {
        margin: 0 -20px 0 -10px !important;
        text-indent: 20px;
        border-top: 1px solid $gray;
        ul.menu {
          li {
            padding: 5px !important;
            border-top: none;
          }
        }
      }
    }

    #block-utilitymenu-3 ul.nav.menu {
      li {
        ul.menu {
          li {
            padding: 5px !important;
          }
        }
      }
    }
  }
}

#block-utilitymenu-3,
.region-secondary-menu {
  a {
    font-weight: 100;
  }

  a:active,
  a:focus {
    border-color: $white !important;
    background-color: transparent !important;
  }

  // the dropdown needs its own special sauce as does the ul that opens
  .nav .open > a {
    background-color: transparent !important;
  }
  li.expanded.dropdown.open {
    ul.dropdown-menu {
      margin-top: 20px;
      padding: 10px;
      background-color: $dart-green !important;
      li {
        padding: 5px 0 !important;
      }
      a {
        display: inline;
        color: $white !important;
        background-color: transparent !important;
      }
      a:hover,
      a:focus,
      a:active {
        margin-bottom: -1px !important;
        border: none;
        border-bottom: 1px solid $white;
      }
    }
  }
}
.region-secondary-menu {
  li.expanded.dropdown.open {
    // the caret at the top of the expanded dropdown
    ul.dropdown-menu::before {
      @extend %icon-expand-less;

      position: absolute;
      top: -18px;
      right: 25px;
      color: $dart-green;
      font-size: em($base-font-size * 1.5);
    }
  }
}

// closer for mobile menu
.mobile-menu-closer {
  float: right;
  margin-right: -10px;
  margin-bottom: 20px;
  padding: 0 8px 3px;
  color: $off-white;
  border: 1px solid $off-white;
  &:hover {
    cursor: pointer;
  }
}

// add the icons for the right hand links
#block-utilitymenu-3,
.region-secondary-menu {
  ul {
    li {
      a::before {
        margin-right: 3px;
        vertical-align: bottom;
        color: $off-white;
        font-family: $font-primary-icons;
        font-size: em($base-font-size * 0.8);
        font-weight: 100;
      }
      a[href*="libcal"]::before {
        @extend %icon-calendar;
      }
      a[href*="campus"]::before {
        @extend %icon-rss-feed;
      }
      a[href*="accounts"]::before {
        @extend %icon-user;
      }
    }
  }
}

// side menus - navigation
.region-sidebar-first {
  border-collapse: collapse;
  h2 {
    margin-bottom: 0;
    padding-top: 10px;
    padding-right: $base-font-size;
    padding-bottom: 10px;
    border-top: 1px solid $gray-medium;
    border-bottom: 1px solid $gray-medium;
    font-size: $base-font-size * 1.25;
    a {
      color: $black;
      &:focus {
        outline: solid 3px $yellow;
      }
    }
  }
  .quick-links-menu {
    position: relative;
    display: block;
    padding: 10px 0;
    letter-spacing: 0.6px;
    text-transform: uppercase;
    font-size: $base-font-size * 0.75;
    font-weight: bold;
  }

  ul.dropdown-menu > li > a {
    white-space: normal;
  }

  li {
    font-size: em($base-font-size * 0.95);
    &.expanded {
      border-bottom: 1px solid $forest-green;
      a {
        border-bottom: none;
      }
    }
    a {
      padding-left: 0;
      letter-spacing: 0.6px;
      text-transform: uppercase;
      font-size: $base-font-size * 0.75;
      font-weight: bold;
      line-height: 130%;
      &:hover {
        outline-color: transparent !important;
      }
      &:hover,
      &:focus {
        background-color: transparent;
        &::after {
          @extend %icon-chevron-right;

          float: right;
          margin-right: -20px;
          font-weight: $font-weight-strong;
        }
      }
    }
    &.active:not([class*="active-parent"]) {
      > a {
        &::after {
          @extend %icon-chevron-right;

          float: right;
          margin-right: -20px;
          font-weight: $font-weight-strong;
        }
      }
    }
    &.expanded ul.dropdown-menu {
      position: static;
      display: inline;
      float: none;
      border: none;
      box-shadow: none;
      li {
        a {
          padding-left: $base-font-size * 1.5;
          border-bottom: none !important;
          font-size: $base-font-size * 0.6;
          &:not(.is-active)::after {
            color: transparent;
          }
          &:hover::after,
          &:focus::after {
            @extend %icon-chevron-right;

            float: right;
            margin-right: -20px;
            color: $dart-green;
            font-weight: $font-weight-strong;
          }
        }
      }
    }
    a {
      color: $dart-green !important;
      border-collapse: collapse;
      border-bottom: 1px solid $forest-green;
    }
  }

  // Override bootstrap submenu styles
  .caret {
    display: none;
  }
  .dropdown-menu > .active > a {
    background: transparent;
  }
}

@media (max-width: em(991px)) {
  header,
  .primary-secondary-menus-inner-wrapper {
    width: 100% !important;
  }
}

@media (max-width: em(767px)) {
  #universal-top-menu {
    margin-bottom: 3px;
  }
  .btn-nav-dartmouth a span.btn-txt {
    display: inline !important;
  }
  .primary-secondary-menus-inner-wrapper {
    min-height: unset !important;
    padding-top: 5px;
    padding-bottom: 5px;
    ul {
      li {
        a {
          font-size: em(14px) !important;
        }
      }
    }
  }

  // remove the caret from the top of the expanded menu in mobile view
  .region-secondary-menu {
    ul {
      li.expanded.dropdown.open {
        ul.dropdown-menu::before {
          content: "";
        }
      }
    }
  }

  .dcl-sidebar-first {
    padding: 0 24px 24px 24px;
    background-color: $teal;
    .region-sidebar-first {
      text-align: left;
      h2 {
        padding-right: 0;
        padding-left: 15px;
        border-top: none;
      }
      ul li ul.menu {
        text-align: left;
        a {
          margin-left: $base-font-size * 0.6;
        }
      }
    }
  }
}

// footer copyright
.site-footer__bottom .region-footer-copyright ul {
  float: none;
  text-align: center;

  li {
    display: inline-block;
  }
}

// Code for the new global menu using dropdown to show submenus Dec 2018
.primary-secondary-menus-inner-wrapper .navbar-toggle {
  padding: 15px 0;
}

// WAVE tool fix
.region-primary-menu nav.column-menu {
  align-self: center;
  justify-self: right;
  > h2 {
    color: $white;
  }
}

.region-primary-menu nav.column-menu .dropdown:hover > .dropdown-menu,
.region-primary-menu nav.column-menu .dropdown.hover > .dropdown-menu {
  position: absolute;
  z-index: 1010;
  left: 0;
  width: 100%;
  min-width: 100%;
  padding: $base-font-size $base-font-size * 2.33333;
  a {
    color: $white;
    &:hover {
      color: $white;
    }
  }
}

.region-primary-menu nav.column-menu .dropdown.account {
  a {
    color: $white !important;
    &:hover {
      color: $white;
      outline: none;
    }
  }
  span.account {
    vertical-align: middle;
  }
  &:hover,
  &.hover {
    > a,
    > span {
      text-decoration: none;
      color: $white !important;
      .account::after {
        color: $white !important;
      }
    }
  }
}
.region-primary-menu nav.column-menu .dropdown.account > .dropdown-menu {
  top: 83%;
  display: block;
  width: 25%;
  padding-left: 3em;
  li {
    display: list-item;
    a {
      font-size: $base-font-size * 0.9;
    }
  }
}
.region-primary-menu nav.column-menu .dropdown.account:hover > .dropdown-menu,
.region-primary-menu nav.column-menu .dropdown.account.hover > .dropdown-menu {
  z-index: 1010;
  right: 5px;
  left: auto;
  display: block;
  width: 25%;
  min-width: 0;
  padding-left: 3em;
  li {
    display: list-item;
    list-style: none;
    text-align: left;
    color: $dart-green;
    a {
      display: inline;
      white-space: normal !important;
      color: $white !important;
      &:hover,
      &:focus,
      &:active {
        margin-bottom: 0 !important;
        text-decoration: none;
        color: $white !important;
        border-bottom: 1px solid $spring-green;
      }
    }
    &:hover,
    &.hover,
    &:focus {
      color: $dart-green;
    }
  }
}

.region-primary-menu nav.column-menu > ul.menu {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(1%, max-content));
  margin: 0;
  padding-top: 0;
  column-gap: $base-font-size;
  &::after,
  &::before {
    display: none;
  }
  .icon-expand-more::after {
    vertical-align: baseline;
    line-height: 0;
  }
  > li {
    position: static;
    display: table-cell;
    padding: $base-font-size $base-font-size * 0.5 !important;
    font-size: 16px;
    line-height: normal;

    .caret {
      margin-left: 0;
      border-top: 8px dashed;
      border-top: 8px solid \9;
      border-right: 4px solid transparent;
      border-left: 4px solid transparent;
    }
    > a.dropdown-toggle,
    > span.dropdown-toggle {
      float: left;
    }
    &:hover,
    &.hover {
      cursor: pointer;
      background-color: $forest-green;
      .caret,
      .icon-expand-more::after {
        @extend %icon-expand-less;

        color: $white;
      }
      > a,
      > a:focus,
      > a:active,
      > a:hover {
        text-decoration: none;
        color: $white !important;
        border-bottom: 1.5px solid $spring-green;
      }
    }
  }
  li .icon-tab-toggle {
    color: $white;
    font-size: 2rem;
  }
}

.region-primary-menu nav.column-menu .dropdown > .dropdown-menu {
  position: absolute;
  z-index: -9999;
  top: 83%;
  left: -9999em;
  display: grid;
  grid-template-rows: fit-content(100%) 1fr;
  width: 100%;
  padding: 2rem;
  color: $white;
  border: none;
  background-color: $forest-green;

  .item-description {
    margin: 1rem 1rem 1rem 0;
    font-size: $base-font-size * 0.8;
  }
  li {
    max-width: 100%;
  }

  .submenu-group {
    max-width: 100%;
    padding-top: 0;
    list-style-type: none;
    text-align: left;
    border-left: none;

    ul {
      display: block;
      margin: 1rem 0 0 0 !important;
      padding-left: 0;
      column-count: 3;
      column-gap: 2rem;

      li {
        position: relative;
        display: block;
        width: inherit;
        width: 100%;
        max-width: 100%;
        margin-bottom: 50px;
        padding: 0 !important;
        text-align: left;
        page-break-inside: avoid;
        break-inside: avoid;
        &:nth-of-type(2n) {
          margin-bottom: 0;
        }
        a {
          color: $white !important;
          border-bottom: 1.5px solidtransparent;
          font-size: $base-font-size;
          font-weight: bold;
          &:hover,
          &:focus,
          &:active {
            color: $white !important;
            border-bottom: 1.5px solid $spring-green;
          }
        }
        ul.dropdown-menu,
        .dropdown-menu {
          position: relative;
          top: 0;
          left: 0;
          display: block;
          float: none;
          padding: 0 !important;
          column-count: 1;
          border: none;
          background-color: transparent;
          box-shadow: none;
          li {
            display: inline-block;
            width: 100%;
            margin: 0;
            padding: 0 !important;
            text-align: left;
            text-indent: 1rem;
            a {
              letter-spacing: normal;
              text-transform: capitalize;
              font-size: $base-font-size;
              font-weight: normal;
            }
            ul.dropdown-menu {
              margin: 0 !important;
            }
          }
        }
        span {
          display: block;
        }
      }
    }
  }

  > li.additional-links {
    max-width: 100%;
    margin-left: 0;
    padding-left: 2rem;
    text-align: left;
    font-size: 1.2em;
    .additional-links-header {
      display: flex;
      align-items: center;
      margin-bottom: 9px;
      &::after {
        flex: 1;
        height: 1px;
        margin-left: 1rem;
        content: "";
        background-color: $white;
      }
    }
    > ul {
      max-width: 100%;
      margin: 0 !important;
      padding: 0;
      list-style-type: disc;
      li {
        margin: 0 !important;
        padding: 0 !important;
        list-style: none;
        text-align: center;
        &:hover,
        &.hover,
        &:focus {
          cursor: default;
        }
        span {
          display: block;
          float: none;
          text-align: left;
          font-size: $base-font-size;
        }
        a {
          display: inline;
          white-space: normal !important;
          color: $white;
          border-bottom: 1.5px solid;
          font-size: $base-font-size * 0.8;
          &:hover,
          &:focus,
          &:active {
            margin-bottom: 0 !important;
            text-decoration: none;
            color: $white;
            border-bottom: 1.5px solid $spring-green;
          }
          &:hover {
            outline: none;
          }
        }
        &.active a {
          background-color: transparent;
        }
      }
      > li {
        margin: 0 !important;
        padding: 0 !important;
      }
      ul.dropdown-menu,
      .dropdown-menu {
        position: static;
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-width: 100%;
        margin-left: 0 !important;
        padding: 0;
        border: none;
        background-color: transparent;
        box-shadow: none;
        &::after,
        &::before {
          display: none;
        }
        li {
          margin: 6px 0 14px 20px;
          padding: 0;
          list-style: none;
          text-align: left;
          color: $white;
          &:hover,
          &.hover,
          &:focus {
            color: $white;
          }
        }
      }
    }
  }
}

.region-primary-menu.dcl-scrolled nav.table-menu > ul.nav > li.dropdown {
  padding-top: 18px !important;
  padding-bottom: 14px !important;
  > .dropdown-toggle {
    &.active-hover::before {
      bottom: -16px;
      left: -50px;
      padding: 15px 0 0 50px;
    }
    &.active-hover::after {
      right: -50px;
      bottom: -16px;
      padding: 15px 50px 0 0;
    }
  }
}

// table layout for menu items to allow vertical aligmment of
// titles. integrate with above if needed most likely we can delete
// this
.region-primary-menu
  nav.column-menu.table-menu
  .dropdown
  > .dropdown-menu
  .submenu-group {
  ul {
    display: grid;
    grid-template-rows: repeat(2, fit-content(100%));
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: $base-font-size;
    width: 100%;
    max-width: 100%;
    column-count: unset;
    column-gap: unset;
    li {
      margin-bottom: 0 !important;
      padding: 0 16px 0 0 !important;
      vertical-align: top;
    }
  }
}

.region-primary-menu > nav.table-menu.column-menu {
  > ul.nav > li.dropdown.account.hover > .dropdown-menu {
    display: block !important;
  }
}
.icon-close-times-remove,
.icon-menu {
  color: $white;
  font-size: $base-font-size * 1.5;
  line-height: 0;
}

.fade-in-dropdown {
  transition-delay: 300ms;
  animation-name: fade-in-opacity;
  animation-duration: 600ms;
  animation-timing-function: ease-in;
  animation-iteration-count: 1;
  opacity: 1;
}

@keyframes fade-in-opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.region-primary-menu
  nav.column-menu
  .dropdown
  > .dropdown-menu
  > li.additional-links
  > ul
  li
  span {
  display: block;
  float: none;
  text-align: left;
}

.region-primary-menu
  nav.column-menu.grid-menu
  .dropdown
  > .dropdown-menu
  .submenu-group {
  ul {
    @include grid-rows($dcl-nav-menu-rows);

    display: grid;
    -ms-grid-columns: minmax(33%, 33%) minmax(33%, 33%) minmax(33%, 33%);
    grid-template-columns: repeat(3, minmax(33%, 33%));
    grid-auto-flow: column;
    column-count: unset;
    column-gap: unset;

    li {
      // IE specific placement
      @include ie-grid-placement($dcl-nav-menu-rows, $dcl-nav-menu-columns);

      width: 100% !important;
      margin-bottom: 0 !important;
      padding: 0 16px 0 0 !important;
      vertical-align: top;

      &:nth-of-type(2n) {
        margin-bottom: inherit;
      }
    }
  }
}

// body disable for mobile nav
html.mobile-nav-open,
body.mobile-nav-open {
  .dcl-fixed-header {
    overflow: intiial;
    .region-mobile-menu {
      overflow-x: hidden;
      overflow-y: scroll;
    }
  }
}

.page-node-type-hub-page,
.page-node-type-page {
  .hero-menu-wrapper {
    display: none;
  }
}

@media (max-width: em(991px)) {
  .navbar-toggle {
    display: block;
  }
  .primary-secondary-menus {
    float: left;
    .navbar-header {
      float: none;
      align-self: center;
      button.navbar-toggle:focus {
        outline: $off-white solid 3px;
        outline-offset: 2px;
      }
    }

    #primary-secondary-menus-wrapper {
      display: grid;
      grid-template-columns: 1fr fit-content(100%);
      gap: 36px;
      min-height: 72px;
    }
    .region-primary-menu {
      grid-template-columns: fit-content(100%) fit-content(100%);
      .nav,
      .column-menu,
      nav.column-menu > ul.menu {
        display: none;
      }
      .search--dcl-unified-search-form {
        justify-self: right;
        width: 50%;
      }
    }
    .navbar-toggle {
      margin: 0;
      padding: 9px 0;
    }
    .icon-close-times-remove {
      display: none;
    }
    #global-mobile-menu {
      .search--dcl-unified-search-form {
        display: none;
      }
      .region-mobile-menu nav ul.menu > li:first-of-type {
        border: none;
      }
    }
  }

  @media (max-width: em(767px)) {
    .page-node-type-hub-page,
    .page-node-type-page {
      .hero-menu-wrapper {
        display: block;
      }
    }
    .primary-secondary-menus {
      #primary-secondary-menus-wrapper {
        grid-template-columns: fit-content(100%) 1fr;
        .search--dcl-unified-search-form {
          display: none;
        }
      }
      #global-mobile-menu {
        .search--dcl-unified-search-form {
          display: block;
        }
        .region-mobile-menu nav ul.menu > li:first-of-type {
          border: 1px solid $white;
        }
      }
    }
  }

  // IE and edge have scroll bars, so move the close alignment
  @mixin ie-scrollbar-spacer {
    .primary-secondary-menus .icon-close-times-remove {
      margin-right: 16px;
    }
  }

  // IE and edge have scroll bars, so move the close alignment
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    @include ie-scrollbar-spacer;
  }

  @supports (-ms-accelerator: true) {
    @include ie-scrollbar-spacer;
  }

  @supports (-ms-ime-align: auto) {
    @include ie-scrollbar-spacer;
  }
}

// stylelint-disable-next-line order/order
#global-mobile-menu {
  position: absolute;
  z-index: 10;
  top: 72px;
  right: 0;
  width: 100%;
  .region-mobile-menu {
    background-color: $forest-green;
    .search--dcl-unified-search-form {
      float: none;
      padding-right: 4px;
      padding-left: 4px;
      .input-group {
        width: 100%;
        padding: 10px 0 10px 27px;
      }
    }

    .dropdown-menu > .active > a {
      background-color: transparent !important;
    }

    nav ul.menu {
      max-width: 100%;
      > li {
        margin-left: 18px;
        border-collapse: collapse;
        border-bottom: 1px solid $white;
        &.submenu-group {
          border-bottom: none;
        }
        &.account {
          .dropdown-menu li a {
            padding-left: 32px;
            font-size: $base-font-size * 0.9;
          }
        }
      }
    }
    nav ul li {
      position: relative;
      display: block;
      clear: both;
      width: 100%;
      margin: 0 0 8px 0;
      > a,
      span.dropdown-toggle {
        float: left;
      }
      > a:hover {
        text-decoration: underline;
      }
      a:focus {
        outline: solid;
      }
      > span.dropdown-toggle {
        position: relative;
        display: block;
        padding: 10px 15px;
      }
      &.additional-links {
        display: block;
        max-width: 100%;
      }
      .mobile-open-show {
        float: left;
        margin: 12px 0 8px 0;
        padding: 0;
        color: $white;
        font-size: 16px;
        &.mobile-nav-open {
          transform: rotate(180deg);
        }
        &:hover {
          cursor: pointer;
        }
      }
      > .dropdown-menu {
        position: absolute;
        left: -999em;
      }
      &.hover > .dropdown-menu {
        position: static;
        display: block !important;
      }
      .dropdown-menu {
        width: 100%;
        padding: 0;
        color: $white;
        border: none;
        border-radius: 0;
        background-color: $forest-green;
        -webkit-box-shadow: none;
        box-shadow: none;
        &.mobile-nav-open {
          position: static;
          display: block !important;
        }
        li {
          width: 100%;
          border-bottom: solid 1px $white;
          a {
            width: auto;
            padding: 10px 15px;
            color: $white !important;
            &:hover {
              color: $white !important;
            }
          }
        }
        .item-description {
          display: none;
        }
        li.submenu-group > ul {
          padding: 0;
          > li a {
            float: left;
            padding-left: 32px;
          }
          > li:last-of-type > a {
            border-bottom: none;
          }
          li.expanded.dropdown {
            width: 100%;
            border-bottom: 1px solid $gray-line-deco;
            a {
              position: relative;
              display: block;
              float: left;
              width: auto;
              border: none;
              border-collapse: collpse;
            }
            .mobile-open-show {
              color: $dart-green;
            }
            ul {
              background-color: $spring-green;
              li a {
                margin-left: 16px;
                &:hover {
                  text-decoration: underline;
                }
              }
            }
          }
        }
      }
    }
    .caret {
      border-top: 8px dashed;
      border-top: 8px solid \9;
      border-right: 8px solid transparent;
      border-left: 8px solid transparent;
      &.mobile-nav-open {
        transform: rotate(180deg);
      }
    }
  }
}

// project page overrides
.dcl-project .dcl-fixed-header,
.dcl-dcl-project .dcl-fixed-header {
  .search--dcl-unified-search-form,
  .region-primary-menu .menu--global-dropdown-menu,
  .icon-close-times-remove {
    display: none;
  }
  .region-primary-menu {
    float: left;
    width: auto;
  }
  .primary-secondary-menus {
    #primary-secondary-menus-wrapper {
      float: left;
      width: 100%;
    }
    .navbar-header {
      float: right;
    }
  }
  .primary-secondary-menus-inner-wrapper {
    min-height: unset !important;
    padding: 5px 15px;
  }
  .navbar-toggle {
    position: relative;
    display: block;
    float: right;
    margin-top: 8px;
    margin-right: 15px;
    margin-bottom: 8px;
    padding: 15px 0;
    border: 1px solid transparent;
    border-radius: 4px;
    background-color: transparent;
    background-image: none;
  }
  #global-mobile-menu {
    max-width: 400px;
    .search--dcl-unified-search-form {
      display: table-cell;
    }
  }
}
